@charset "UTF-8";
/* Mixins */
/* ========================
 * Head section (jumbotron) 
 * ======================== */
.head-section-slide-1 {
  background-image: url(../img/bg-header-1.jpg); }

.head-section-slide-2 {
  background-image: url(../img/bg-header-2.jpg); }

.head-section-slide {
  background-repeat: no-repeat;
  background-size: cover;
  color: #FFF;
  position: relative;
  height: 100vh;
  display: flex !important;
  justify-content: center;
  align-items: center; }
  .head-section-slide-overlay {
    display: block;
    z-index: 100;
    position: absolute;
    background: rgba(0, 0, 0, 0.4);
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }
  .head-section-slide__h1 {
    position: relative;
    z-index: 101;
    margin: 20px 0; }
  .head-section-slide__p {
    position: relative;
    z-index: 101;
    max-width: 750px;
    margin: 10px auto 30px;
    line-height: 25px; }
  .head-section-slide__video-btn {
    position: relative;
    z-index: 101;
    display: block;
    max-width: 100px;
    margin: 20px auto;
    background: transparent;
    border: none; }
  .head-section-slide__btn {
    position: relative;
    z-index: 101; }

.head-section .slick-prev {
  top: 0;
  left: 0;
  transform: translate(0, 0);
  height: 100%;
  width: 70px; }
  .head-section .slick-prev:before {
    content: " ";
    display: block;
    width: 70px;
    height: 100vh;
    background: linear-gradient(to left, rgba(255, 255, 255, 0.008), rgba(255, 255, 255, 0.4));
    position: absolute;
    top: 0;
    left: 0; }
  .head-section .slick-prev .fa {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: rgba(255, 255, 255, 0.8); }

.head-section .slick-next {
  top: 0;
  right: 0;
  transform: translate(0, 0);
  height: 100%;
  width: 70px; }
  .head-section .slick-next:before {
    content: " ";
    display: block;
    width: 70px;
    height: 100vh;
    background: linear-gradient(to right, rgba(255, 255, 255, 0.008), rgba(255, 255, 255, 0.4));
    position: absolute;
    top: 0;
    right: 0; }
  .head-section .slick-next .fa {
    position: absolute;
    top: 50%;
    right: 50%;
    transform: translate(50%, -50%);
    color: rgba(255, 255, 255, 0.8); }

/* ========================
 * Override slick theme
 * ======================== */
.slick-dots {
  position: absolute;
  bottom: 20px;
  display: block;
  width: 100%;
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center; }

.slick-dots li {
  position: relative;
  display: inline-block;
  width: 20px;
  height: 20px;
  margin: 0 5px;
  padding: 0;
  cursor: pointer; }

.slick-dots li button {
  font-size: 0;
  line-height: 0;
  display: block;
  width: 20px;
  height: 20px;
  padding: 5px;
  cursor: pointer;
  color: transparent;
  border: 0;
  outline: none;
  background: transparent; }

.slick-dots li button:before {
  font-size: 50px;
  line-height: 20px;
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  content: '•';
  text-align: center;
  opacity: .25;
  color: #FFF;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.slick-dots li.slick-active button:before {
  opacity: .75;
  color: #FFF; }

.slick-prev, .slick-next {
  font-size: 30px;
  z-index: 1000;
  color: rgba(0, 0, 0, 0.5);
  line-height: 0;
  position: absolute;
  top: 50%;
  display: block;
  width: 20px;
  height: 20px;
  padding: 0;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  cursor: pointer;
  border: none;
  outline: none;
  background: transparent; }

.slick-next {
  right: 10px; }

.slick-prev {
  left: 10px; }

/* ========================
 * Sections 
 * ======================== */
