/* Modal */
.modal-overlay {
  visibility: hidden;
  opacity: 0;
  position: fixed;
  z-index: 1001;
  background: rgba(0, 0, 0, 0.75);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: .2s; }
  .modal-overlay.active {
    visibility: visible;
    opacity: 1; }
  .modal-overlay-close {
    font-size: 4em;
    color: rgba(255, 255, 255, 0.5);
    position: absolute;
    top: 10px;
    right: 40px;
    z-index: 1003;
    cursor: pointer; }

.modal-media {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 1002;
  background: #FFF;
  transform: translate(-50%, -50%) scale(0, 0);
  max-width: 650px;
  width: 100%;
  height: auto;
  padding: 10px;
  border-radius: 10px;
  transition: .2s; }
  .modal-media.active {
    transform: translate(-50%, -50%) scale(1, 1); }
  .modal-media-video__iframe {
    width: 100%; }

